import axios from "axios";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import AnimeTab from "../component/animeTab";

const Anime=()=>{
    let {anime}=useParams()
    const[animeData,setanimeData]=useState({})

    useEffect(()=>{
         (async()=>{
            const res = await axios.get(`https://anime-facts-rest-api.herokuapp.com/api/v1/${anime}`)
            setanimeData(res.data)


        })()
        
    },[anime])

   
    return(
        <div>
            {/* <h1 className="font-semibold">{anime}</h1> */}
            <div className="md:flex">
            <div>
                <img src={animeData.img} className="w-96 md:sticky md:top-0 mt-16"/>
            </div>
            <div>
            {
                animeData.data !== undefined && animeData.data.map((eachitem)=>{
                   return <AnimeTab key={eachitem.fact_id} facts={eachitem.fact}/>
                })
        
            }
            </div>
        </div>
        </div>
    )
}

export default Anime;