
import Tab from '../component/Tab'
import '../css/App.css'
import axios from 'axios'
import { useEffect, useState } from 'react'

const Home = () => {
  const [anime, setanime] = useState([])
  const[input,setinput]=useState("")

  useEffect(() => {
    const fun = async () => {
      const res = await axios.get('https://anime-facts-rest-api.herokuapp.com/api/v1')

      //  console.log( setanime(res.data.data))
      setanime(res.data.data)
      // console.log(res)

      
  }
  fun();
},[])

  return (<div className='bg-purple-50 flex flex-col  items-center min-h-screen'>
        <div className='font-bold  justify-center items-center flex'>
        <h1 className='text-xl font-semibold'>Anime Facts</h1>
        </div>
        <input type="text" className=' m-4 p-3 outline-none rounded-md ring 2 ring-purple-200' placeholder='Anime name.....' onChange={(event)=>{
          // const{value}=event.target
          // let filter=anime.filter((anime)=>{
          //   const{anime_name}=anime
          //   if(anime===""){
          //     return(anime)
          //   }else if(anime_name.toLowerCase().includes(value.toLowerCase()))
          //   return(
          //     anime
          //   )
            
          // })
          // setanime(filter)
          setinput(event.target.value)
        }}/>
        <div className='flex flex-wrap justify-center items-center  '>
        {
           anime.filter((val)=>{
             const{anime_name}=val
             console.log(val.anime_name)
             if(input===""){
               return val
             }else if(anime_name.toLowerCase().includes(input.toLowerCase())){
               return val
             }

           }).map((eachitem,i)=>{
            return(
              <Tab key={i}name={eachitem.anime_name} img={eachitem.anime_img}/>
            
            )
           })
        }
        
      </div>
  </div>)

}

export default Home