import { Link } from "react-router-dom/cjs/react-router-dom.min";

const Tab=({name,img})=>{
    return(
        <Link to={`/${name}`}className=" m-11 hover:text-blue-600">
          
            <img src={img} alt="anime_name" className="h-64 w-48 rounded-lg filter shadow-sm cursor-pointer ring 2 ring-black hover:ring-purple-200 ring-opacity-50"/>

            <h1 className="font-semibold text-center">{name}</h1>

            
        </Link>
    )

}

export default Tab;