import { useLocation } from "react-router-dom";
import {
    Link
  } from "react-router-dom";
const Header=()=>{
    const{pathname}=useLocation()
    console.log(pathname)
    return(
        <div className="flex justify-between p-2 shadow-lg bg-purple-200 cursor-pointer">
            <h1></h1>
            <div className="flex font-semibold mx-9 my-2">
            <Link to="/" className={`mx-2 hover:text-blue-600 font-semibold text-xl${pathname==="/"&&"underline"}`}>Home</Link>
            <Link to="/about" className={`mx-2 hover:text-blue-600 font-semibold text-xl${pathname==="/about"&&"underline"}`}>About</Link>
            <Link to="/services"className={`mx-2 hover:text-blue-600 font-semibold text-xl${pathname==="/services"&&"underline"}`}>Services</Link>
            </div>
        </div>
    )
}

export default Header;